













import Vue from 'vue';
import Component from 'vue-class-component';
import { DEFAULT_WIDTH, SX_WIDTH } from './config';
import { Store } from 'vuex';

@Component
export default class App extends Vue {
    public width: number = DEFAULT_WIDTH;
    public scale: number = 1;

    private timer: any = null;

    public mounted() {
        this.scaleRoot();
        window.addEventListener('resize', this.scaleRoot.bind(this), false);
    }

    public created() {
        setInterval(() => {
            this.$http
                .get('/api/alarm/getall', {
                    pageSize: 99999999,
                    currentPage: 1
                })
                .then(res => {
                    res.pagedData.datas.forEach((v: IAlarm) => {
                        this.$notify.warning(
                            `标签${v.tagNo}异常。\n${v.alarmMsg}`
                        );
                    });
                })
                .catch(console.log);
        }, 1000);
    }

    private scaleRoot() {
        if (!this.timer) {
            this.timer = setTimeout(() => {
                const rootWidth = document.body.clientWidth;
                this.width = DEFAULT_WIDTH;
                if (rootWidth <= SX_WIDTH) {
                    this.width = SX_WIDTH;
                }
                this.scale = document.body.clientWidth / this.width;
                this.$store.commit('setRootScale', this.scale);

                this.setBodyHeight();
            }, 300);
        }
    }

    private setBodyHeight() {
        const root = <HTMLElement>this.$refs.root;
        const setHeight = () => {
            if (root && root.offsetHeight) {
                document.body.style.height =
                    root.offsetHeight * this.scale + 'px';

                this.timer = null;
            } else {
                requestAnimationFrame(setHeight);
            }
        };

        setHeight();
    }
}
